import b2bFooter from "../../components/b2bfooter.vue";
export default {
	components: {
		b2bFooter
	},
	data: function(){
		return {
			pageLoad: false,
			pageHide: false,
			pageData: {}
		}
	},
	onLoad: function (option) {
		uni.setNavigationBarTitle({
			title: "个人中心",
		})
		this.getPage();
	},
	onShow: function () {
		if (this.pageHide) {
			this.pageHide = false;
			this.getPage();
		}
	},
	onHide: function () {
		this.pageHide = true;
	},
	methods: {
		goWeb:function(url){
			uni.navigateTo({
				url:"../../pages/web/index?url="+encodeURI(url)
			})
		},
		gourl: function (url) {
			uni.navigateTo({
				url: url,
			})
		},
		getPage: function () {
			var that = this;
			uni.request({
				url: that.app.apiHost + "/module.php?m=b2b_user&ajax=1",
				data: {
					authcode: that.app.getAuthCode()
				},
				success: function (res) {
					if (res.data.error == 1000) {
						/*
						uni.navigateTo({
							url: "../login/index",
						})
						*/
					} else {
						that.pageLoad = true;
						that.pageData = res.data.data;
					}


				}
			})
		},
		loginOut:function(){
			var that=this; 
			uni.request({
				url:that.app.apiHost+"?m=login&a=logout&ajax=1",
				data:{
					"authcode":that.app.getAuthCode(),
					"fromapp":that.app.fromapp()
				},
				success:function(res){
					that.app.setAuthCode("");
					that.app.setAuthCodeLong("");
					that.app.goHome();	
				}
			})
		}
	},
}
